/*
 * 后台框架顶部栏目
 * -------------------------
 */
body { height: auto; }
html { height: auto; }
#xqkeji-header {
  height: $header-height;
  >.col-2{
    width: $left-width;
  }
  >.col-10{
    width: $right-width;
  }
  &.mini{
    >.col-2{
      width: $mini-width;
      padding-left: 15px;
      padding-right: 0;
      .xq-desc{
        display: none;
      }
    }
    >.col-10{
      width: calc(#{$max-vw} - #{$mini-width});
      .col-2{
        padding-right: 0 !important;
      }
    }
  }
  a {
    text-align: center;
    color: rgb(255, 255, 255);
    span:first-child
    {
      font-size: 24px;
    }
  }
  #xqkeji-top-nav
  {
    a{
      color: rgb(255, 255, 255);
    }
  }
  #xqkeji-top-menu{
    .btn{
      color: rgb(255, 255, 255);
    }

    .dropdown-menu{
      min-width: auto;
      .dropdown-item
      {
        color: rgb(0, 0, 0);
      }
    }

  }

}
