#xqkeji-content{
  height: calc(#{$max-vh} - #{$header-height} - #{$footer-height});
  min-height: calc(#{$max-vh} - #{$header-height} - #{$footer-height});
  margin-top: $header-height;
  margin-bottom: $footer-height;
  width: $right-width;
  &.mini{
    width: calc(#{$max-vw} - #{$mini-width} + 10px);
  }

}


#xqkeji-tab-header{
  height: $tab-header-height;
  background: #fafafa;
  border-bottom: solid 2px #198754;
  font-size: 12px;
  border-top: 1px solid #ddd;
  #xqkeji-tab-left{
    width: $tab-header-btn-width;
    line-height: 23px;
    max-height: $tab-header-height;
    text-align: center;
    color: #475059;
    background: #fff;
    border: 0;
    z-index: 99;
  }
  #xqkeji-tab-right{
    @extend #xqkeji-tab-left;
  }
  #xqkeji-tab-fullscreen{
    @extend #xqkeji-tab-left;
  }
  #xqkeji-tab-nav{
    width: calc(100% - #{$tab-header-btn-width} - #{$tab-header-btn-width});
    max-height: $tab-header-height;
    overflow: hidden;
    line-height: 23px;
    border-bottom-width: 0;
    border-bottom-style: none;
    ul{
      width: max-content !important;
      overflow-y: hidden;
      overflow-x: hidden;
      height: $tab-header-height;
      max-height: $tab-header-height;
      flex-shrink: 0;
      white-space: nowrap;
      :first-child{
        margin-left: 0;
        a{
          padding: .5rem 1rem;
        }
      }
      li{
        a{
          padding: .5rem .2rem .5rem 1rem;
          button.close{
            width: 10px;
            height: 10px;
            padding: 0;
            margin: 0;
            border: 0;
            background-color: inherit;
          }
          button.close span{
            position: relative;
            top: -.8rem;
            font-size: .85rem;
          }
        }
      }
    }
  }
}

#xqkeji-tab-content
{
  height: calc(#{$max-vh} - #{$header-height} - #{$footer-height} - #{$tab-header-height});
  iframe{
    height: calc(#{$max-vh} - #{$header-height} - #{$footer-height} - #{$tab-header-height});
  }
}
.tab-content > .tab-pane
{
  padding-top: 5px;
}
#xqkeji-tab-contentmenu{
  width: max-content;
  display: none;
  position: absolute;
  z-index: 999;
  top: 0;
  left: 0;
}
