/**
 * Component: Sidebar
 * ------------------
 */

#xqkeji-sidebar{
  height: calc(#{$max-vh} - #{$header-height});
  margin-top: $header-height;
  padding-top: .2rem !important;
  z-index: 999;
  width: $left-width;
  p{
    display: inline-block;
    margin-bottom: 0;
  }
  .nav-item {
    > .nav-link {
      margin-bottom: .2rem;
    }
    >ul{
      display: block;
      padding-left: 1rem;
      >li{
        list-style: none;
        >.nav-link{
          padding-left: .5rem;
        }
      }
    }
  }
  &.mini{
    width: calc(#{$mini-width} - 10px);
    p{
      display: none;
      margin-bottom: 0;
    }
    span.float-end
    {
      display: none;
    }
    .nav-item {
      &:hover{
        >a{
          >p{
            display: inline-block;
            position: absolute;
            width: calc(#{$left-width} - #{$mini-width});
            left: calc(#{$mini-width} - 10px);
            padding: .5rem 1rem;
            background-color: inherit;
            z-index: 999;
            margin-top: -8px;
          }
          >span.float-end
          {
            position: absolute;
            left: calc(#{$left-width} - 30px);
            display: block;
            margin-top: -24px;
            z-index: 9999;
          }
        }
        >ul{
          position: absolute;
          display: block;
          width: calc(#{$left-width} - #{$mini-width});
          left: calc(#{$mini-width} - 10px);
          z-index: 9999;
          margin-top: -.2rem;
          background-color: rgba(33, 37, 41, 1);
          p{
            display: inline-block;
            width: auto;
            position: relative;
            left: 0;
            margin-top: 0;
            padding: 0;
          }
          span.float-end{
            display: block;
            position: relative;
            margin-top: 0;
            left: 0;
          }
          ul{
            position: relative;
            width: 100%;
            left: 0;
            margin-top: 0;
          }
        }
      }
      > .nav-link {
        margin-bottom: .2rem;
      }
      >ul{
        display: none;
      }
    }
    > nav > ul > li > ul.d-block{
      display: none !important;
    }
    > nav > ul > li:hover{
      > ul.d-block{
        display: block !important;
      }
    }
  }
  .nav-link{
    color: rgb(255, 255, 255) !important;
  }
}

